.container-special {
  width: 960px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.hero-pages__top-block, .hero-pages__bottom-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top__left-container {
  width: 50%;
  background-color: #25807F;
  height: 106px;
}
.top__right-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 106px;
  width: 50%;
  background-image: url("../../img/logofon.gif");
  background-repeat: no-repeat;
  background-position: top left;
}

.list-info {
  list-style: none;
  padding: 20px 10px;
  margin: 0;
}
.list-info__item {
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  font-weight: 700;
}

.bottom__left-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 56%;
}
.bottom__left-container span {
  font-weight: 700;
}
.bottom__right-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #25807F;
  width: 44%;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 6% 100%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 6% 100%);
}

.icons {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 10px;
}

.numbers {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding-left: 0;
}

.number {
  padding: 5px;
  font-size: 16px;
  font-weight: 700;
}

.email {
  font-weight: 700;
  color: #25807F;
}

.addres {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
}